.container {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary_medium);
}

.item {
    height: 100%;
}

.item a {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.08;
    text-align: center;
    color: #fff;
}

.item .active {
    color: #000;
    background-color: var(--primary_light);
}

@media (pointer:coarse) {
    .container {
        height: 60px;
    }

    .item {
        width: 100%;
    }

    .item a {
        font-size: 18px;
        padding: 0 10px;
    }
}